body {
	margin: 0;
	padding: 0;
	font-size: 1em;
	font-family: 'Open Sans', sans-serif;
	box-sizing: border-box;
}
/*main {
	position: relative;
}*/
header {
	width: 100%
}
header div.main-menu {
	width: 100%
}
header div.main-menu a.logo-header {
	float: left;
	margin: 17px 0 17px 10%;
	width: 10%;
  img {
  	max-width: none;
  	min-width: auto;
  	min-height: auto;
  }
}

header div.main-menu nav.main-nav {
	width: 59%;
	float: left;
}
header div.main-menu nav.main-nav ul.clearfix {
	padding-top: 15px;
}

header div.main-menu nav.main-nav ul.clearfix li{
	display: inline-block;
	margin: 0 0 0 9%;
	float: right;

}
header div.main-menu nav.main-nav ul.clearfix li a {
	font-size: 1em;
	font-family: 'Open Sans', sans-serif;
	color: $font_color;
	text-decoration: none;
	padding-bottom: 30px;
}
header div.main-menu nav.main-nav ul.clearfix li a:hover {
	color: #42b549;
	border-bottom: 2px solid #42b549;
}
header div.main-menu nav.main-nav ul.clearfix li.products {
	position: relative;
	font-size: 1em;
	display: inline-block;
	font-family: 'Open Sans', sans-serif;
	color: $font_color;
	padding-bottom: 20px;
}
header div.main-menu nav.main-nav ul.clearfix li.products ul {
	display: none;
	position: absolute;
	width: 130px;
	background: #fff;
	list-style: none;
	top: 60px;
	left: 0;
	margin: 0;
	padding: 0;
	font-size: 0.7em;
}
header div.main-menu nav.main-nav ul.clearfix li.products ul > li {
	border-bottom: none;
	display: block;
	float: none;
	margin: 0;
	padding: 8px;
	transition: background .5s linear;
}
header div.main-menu nav.main-nav ul.clearfix li.products ul > li:hover {
	background: #f2f2f2;
}

header div.main-menu nav.main-nav ul.clearfix li.products ul > li > a {
	text-decoration: none;
	color: #8c8c8c;
	font-weight: 600;
	border-bottom: none;
	white-space: nowrap;
	text-align: left;
}

header div.main-menu button.login {
	float: right;
	border: none;
	outline: none;
	color: #fff;
	font-size: 0.8em; 
	font-family: 'Open Sans', sans-serif;
	background: $button_color_1;
	padding: 0.6% 2%;
	text-transform: uppercase;
	margin: 23px 10% 0 3%;
	transition: background 1s linear;
}
header div.main-menu button.login:hover {
	background: $button_color_2;
}

section.technology {
	width: 100%;
	height: 680px;
	background: url(../img/ble-backgr.jpg) no-repeat;
	background-size: 100% auto;
	border-top: 1px solid transparent;
}
section.technology div.ble {
	float: left;
	width: 40%;
}
section.technology div.ble h1, h2 {
	color: #fff;
	font-weight: 800;
	text-transform: uppercase;
}
section.technology div.ble h1 {
	font-size: 5em;
	line-height: 20%;
	margin: 39% 0 0 47%;
}
section.technology div.ble h2 {
	font-size: 1.3em;
	margin: 2% 0 20% 47%;
	line-height: 50%;
}
section.technology div.low-energy {
	margin-top: 5%;
	margin-right: 0;
	margin-left: 37%;
	margin-bottom: 0;
	width: 43%;
	background: #fff;
	border: 1px solid #fff;
	border-radius: 20px 0;
	padding: 0 0;
}
section.technology div.low-energy h3 {
	white-space: nowrap;
	color: #172592;
	font-weight: 600;
	text-align: center;
	text-transform: uppercase;
	line-height: 50%;
}
section.technology div.promotional-phrases {
	padding-top: 2%;
}
section.technology div.promotional-phrases div.promotional-block {
	margin: 4% 0 0 -1%;
}

section.technology div.promotional-phrases img {
	display: inline-block;
	margin-right: 1%;
}
section.technology div.promotional-phrases p {
	display: inline-block;
	font-weight: 600;
	color: #fff;
	font-size: 1.25em;
	text-transform: uppercase;
}
section.slider {
	width: 100%;
}
section.slider div.slide-1,
section.slider div.slide-3 {
	background: url(../img/slide-1.jpg) no-repeat;
	background-position: top left;
	background-size: 50% auto;
	width: 100%;
	height: 535px;
	margin: 0;
	box-sizing: border-box;
	padding-right: 10%;
}
section.slider div.slide-1 div.text-block,
section.slider div.slide-3 div.text-block {
	width: 46%;
	float: right;
}
section.slider div.slide-1 h3,
section.slider div.slide-3 h3 {
	color: $font_color;
	font-size: 1.5em;
	line-height: 50%;
	margin: 14% 0 -5% 7%;
	text-align: right;
}
section.slider div.slide-1 h2,
section.slider div.slide-3 h2 {
	color: $font_color;
	font-weight: 800;
	font-size: 3.4em;
	margin: 5% 0 3% 7%;
	text-align: right;
}
section.slider div.slide-1 p,
section.slider div.slide-3 p {
	color: #a0a0a0;
	font-weight: 300;
	text-indent: 18%;
	width: 77%;
	line-height: 1.6;
	margin-right: 0;
	text-align: right;
	float: right;
}
section.slider div.slide-1 button,
section.slider div.slide-3 button {
	color: #fff;
	float: right;
	background: $button_color_2;
	padding: 3% 5%;
	border: none;
	outline: none;
	margin: 4% 0 0 40%;
	letter-spacing: 0.2em;
	transition: background 1s linear;
}
section.slider div.slide-1 button:hover,
section.slider div.slide-3 button:hover {
	background: $button_color_1;
} 

section.slider div.slide-2,
section.slider div.slide-4 {
	background: url(../img/slide-2.jpg) no-repeat;
	background-position: top right;
	background-size: 50% auto;
	width: 100%;
	height: 535px;
}
section.slider div.slide-2 div.text-block,
section.slider div.slide-4 div.text-block {
	width: 46%;
	float: left;
}
section.slider div.slide-2 h3,
section.slider div.slide-4 h3 {
	color: $font_color;
	font-size: 1.5em;
	line-height: 50%;
	margin: 12% 0 -5% 20%;
	text-transform: uppercase;
}
section.slider div.slide-2 h2,
section.slider div.slide-4 h2 {
	color: $font_color;
	font-weight: 800;
	font-size: 3.4em;
	margin: 6% 0 3% 20%;
}
section.slider div.slide-2 p,
section.slider div.slide-4 p {
	color: #a0a0a0;
	font-weight: 300;
	width: 77%;
	line-height: 1.6;
	margin: 3% 0 7% 20%;
}
section.slider div.slide-2 button,
section.slider div.slide-4 button {
	color: #fff;
	background: $button_color_1;
	padding: 3% 5%;
	border: none;
	outline: none;
	margin: 0 0 0 20%;
	letter-spacing: 0.2em;
	transition: background 1s linear;
}
section.slider div.slide-2 button:hover,
section.slider div.slide-4 button:hover {
	background:  $button_color_2;
}

section.slider div.owl-dots {
	width: 10%;
	margin: 0 auto;
}

section.slider div.owl-dot {
	display: inline-block;
	width: 15px;
	height: 15px;
	background: #7f7f7f;
	border: 1px solid #7f7f7f;
	border-radius: 50%;
	margin-right: 7%; 
}
section.slider div.owl-dots div.active {
	background: #000;
	border: 1px solid #000;
}
section.tabs {
	width: 100%;
	height: 670px;
	background-color: #faf7f7;
	background-image: url(../img/bd-img-function.png);
	background-repeat: no-repeat;
	background-position: right 53%;
	background-size: 80% auto;
	border-top: 1px solid #faf7f7;
}
section.tabs div.tabs-block {
	margin: 5% 0 0 14%;
}

section.second-slider {
	display: none;
}

section.tabs div.tabs-block h4 {
	font-weight: 700;
	color: $font_color;
	margin-top: 2%;
}
section.tabs div.tabs-block p {
	font-size: 0.8em;
	color: #7f7f7f;
	padding-left: 9%;
	margin-top: -3%;
}
section.tabs div.management h4::before {
	content: '';
	display: inline-block;
	background: url(../img/reload-icon.png) no-repeat;
	background-size: 100% auto;
	width: 24px;
	height: 24px;
	float: left;
	margin: 0.5% 4% 0 0;
}
section.tabs div.easy-use h4::before {
	content: '';
	display: inline-block;
	background: url(../img/inbox-icon.png) no-repeat;
	background-size: 100% auto;
	width: 24px;
	height: 24px;
	float: left;
	margin: 0.5% 4% 0 0;
}
section.tabs div.quickly h4::before {
	content: '';
	display: inline-block;
	background: url(../img/clock-icon.png) no-repeat;
	background-size: 100% auto;
	width: 24px;
	height: 24px;
	float: left;
	margin: 0 4% 0 0;
}
section.tabs div.management,
section.tabs div.easy-use,
section.tabs div.quickly {
	width: 37%;
	background: transparent;
	border: 1px solid transparent;
	border-radius: 10px;
	padding: 2% 2%;
	margin: 0 0 3% 0;
	box-sizing: border-box;
	transition-property: background, box-shadow;
	transition-duration: 1.5s;
	transition-timing-function: ease-out;
}

section.tabs div.management:hover,
section.tabs div.easy-use:hover,
section.tabs div.quickly:hover {
	width: 37%;
	background: #fff;
	padding: 2% 2%;
	border: 1px solid #fff;
	border-radius: 10px;
	margin: 0 0 3% 0;
	box-sizing: border-box;
	box-shadow: 1px 3px 18px 0 rgba(211,209,209,0.7);  
}
section.tabs div.tabs-block div.management {}

section.download {
	width: 100%;
	height: 670px;
	background: url(../img/don-bg.jpg) no-repeat;
	background-size: 100% auto;
	margin-top: -18px;
	box-sizing: border-box;
}
section.download div.get-started {
	float: right;
	width: 45%;
}
section.download div.get-started h3 {
	color: #fff;
	font-size: 2.3em;
	font-weight: 700;
	text-align: right;
	margin: 34% 25% 0 0;
	white-space: nowrap;
}
section.download div.get-started h3 > span {
	font-weight: 400;
}
section.download div.get-started p {
	color: #fff;
	font-weight: 400;
	text-align: right;
	margin: 2% 25% 0 0;
	line-height: 2;
}
section.download div.get-started button {
	background: transparent;
	width: 34%;
	border: none;
	outline: none;
	float: right;
	display: inline-block;
	margin: 5% 0 0 3%;
	img {
		max-width: none;
  	width: 100%;
		min-width: auto;
		min-height: auto;
	}
}
section.download div.get-started button.google {
	margin-right: 24%;
}
section.news {
	width: 100%;
	height: 685px;
}
section.news div.cellular-iot {
	float: left;
	width: 22%;
	margin: 7% 0 7% 12%;
	padding: 1%;
	box-shadow: 1px 3px 18px 0 rgba(211,209,209,0.7); 
}
section.news div.homeland-security {
	width: 22%;
	display: inline-block;
	margin: 7% 2%;
	padding: 1%;
	box-shadow: 1px 3px 18px 0 rgba(211,209,209,0.7); 
}
section.news div.iot-security {
	float: right;
	width: 22%;
	margin: 7% 12% 7% 0;
	padding: 1%;
	box-shadow: 1px 3px 18px 0 rgba(211,209,209,0.7); 
}
section.news div.cellular-iot img,
section.news div.homeland-security img,
section.news div.iot-security img {
	margin: 0;
	padding: 0;
	max-width: none;
	min-width: auto;
	max-height: auto;
	width: 100%;

} 

section.news div.cellular-iot h4,
section.news div.homeland-security h4,
section.news div.iot-security h4 {
	font-weight: 700;
	color: $font_color;
	margin-left: 2%;
}
section.news div.cellular-iot p,
section.news div.homeland-security p,
section.news div.iot-security p {
	color: #7f7f7f;
	font-size: 0.7em;
} 
section.news div.cellular-iot p.date,
section.news div.homeland-security p.date,
section.news div.iot-security p.date {
	display: inline-block;
	margin: 0 50% 0 2%;
}

section.news div.cellular-iot p.comment,
section.news div.homeland-security p.comment,
section.news div.iot-security p.comment {
	display: inline-block;
}
section.news div.cellular-iot p.article-text,
section.news div.homeland-security p.article-text,
section.news div.iot-security p.article-text {
	font-size: 0.8em;
	margin-left: 2%;
}
section.contact-form {
	width: 100%;
	height: 670px;
	background: url(../img/contact-bg.jpg) no-repeat;
	background-size: 100% 100%;
	border-top: 1px solid transparent;
}
section.contact-form h3 {
	color: #fff;
	font-size: 2em;
	text-align: center;
	margin: 5.5% 0 0 0;
	font-weight: 400;
}
section.contact-form h3 > span {
	font-weight: 700;
}
section.contact-form p {
	color: #fff;
	text-align: center;
}
form[name="contact_form"] {
	display: block;
	margin: 3.7% auto;
	width: 40%;
}
section.contact-form #contact_name {
	margin-right: 2%;
}
section.contact-form #contact_name,
section.contact-form #contact_email {
	display: inline-block;
	padding: 2.5% 3.3%;
	width: 48.4%;
	outline: none;
	color: #bbbbbb;
	font-size: 0.9em;
	border: 1px solid #fff;
	border-radius: 7px;
	box-sizing: border-box;
}
section.contact-form #message {
	display: inline-block;
	width: 100%;
	outline: none;
	font-size: 0.9em;
	border: 1px solid #fff;
	border-radius: 7px;
	padding: 2.5% 3.3%;
	font-size: 1.2em ;
	box-sizing: border-box;
	font-weight: 400;
	color: #bbbbbb;
	margin: 3% 0 2% 0;
}
section.contact-form #contact_name::placeholder,
section.contact-form #contact_email::placeholder,
section.contact-form #message::placeholder {
	color: #bbbbbb;
} 
section.contact-form input[type="submit"] {
	display: block;
	width: 25%;
	margin: 2% auto;
	background: transparent;
	border: 1px solid #fff;
	color: #fff;
	text-transform: uppercase;
	padding: 3% 4%;
	outline: none;
	transition-property: background;
	transition-duration: 1s;
	transition-timing-function: linear;
}
section.contact-form input[type="submit"]:hover {
	background: $button_color_2;
}
footer {
	width: 100%;
	background: #42b549;
	color: #fff;
	padding: 4% 5%;
	box-sizing: border-box;
}

footer ul.company,
footer ul.products,
footer ul.other,
footer ul.contacts {
	display: inline-block;
	list-style-type: none;
	vertical-align: top;
}
footer ul.company,
footer ul.products,
footer ul.other {
	margin-right: 14%;
}

footer ul.company li.main,
footer ul.products li.main,
footer ul.other li.main,
footer ul.contacts li.main {
	padding-bottom: 20px;
}

footer ul.company li > a,
footer ul.products li > a,
footer ul.other li > a,
footer ul.contacts li > a {
	text-decoration: none;
	color: #fff;
	font-size: 0.9em;
}

footer ul.company li.main > a,
footer ul.products li.main > a,
footer ul.other li.main > a,
footer ul.contacts li.main > a {
	font-weight: 700;
}

footer ul.contacts li {
	font-size: 0.9em;
	padding-bottom: 4%;
	white-space: nowrap;
}

footer ul.contacts li:nth-child(1)::before {
	content: '';
	display: inline-block;
	background: url(../img/tel.png) no-repeat;
	width: 10px;
	height: 14px;
	float: left;
	margin: 1% 4% 0 0;
}

footer ul.contacts li:nth-child(2)::before {
	content: '';
	display: inline-block;
	background: url(../img/let-icon.png) no-repeat;
	width: 13px;
	height: 14px;
	float: left;
	margin: 1% 4% 0 0;
}

footer ul.contacts li:nth-child(3)::before {
	content: '';
	display: inline-block;
	background: url(../img/map-icon.png) no-repeat;
	width: 10px;
	height: 14px;
	float: left;
	margin: 0 4% 0 0;
}




#modal_block {
	width: 300px;
	height: 300px;
	border: 2px solid $button_color_1;
	background: #fff;
	position: fixed;
	top: 45%;
	left: 50%;
	margin-top: -150px;
	margin-left: -150px;
	display: none;
	opacity: 0;
	z-index: 20;
	padding: 20px 10px;
}
#modal_block .close {
	width: 17px;
	height: 17px;
	background: url(../img/error.png) no-repeat;
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	display: block;
}
#modal_block p.user-name,
#modal_block p.user-email {
	text-align: center;
	font-weight: 700;
}
#modal_block input[type="submit"] {
	display: block;
	width: 110px;
	margin: 30px auto;
	background: $button_color_2;
	color: #fff;
	padding: 10px 15px;
	outline: none;
	border: none;
}
#modal_block input[type="text"],
#modal_block input[type="email"] {
	width: 200px;
	margin: 7px 0 0 37px;
	outline: none;
	border: 1px solid #cdcaca;
	padding: 10px;
	color: #cdcaca;
}

#overlay {
	z-index: 19;
	position: fixed;
	background: #000;
	opacity: 0.8;
	filter:alpha(opacity=80);
	width:100%; 
	height:100%; 
	top:0; 
	left:0;
	cursor:pointer;
	display:none; 
}





aside {
	position: absolute;
	z-index: 9999;
	top: 0px;
	left: -280px;
	width: 250px;
	height: 500px;
	transition-property: left;
	transition-duration: .7s;
	transition-timing-function: ease-in-out; 
}
.left-pos {
	left: 0;
}
aside div.sidebar {
	width: 100%;
	background: #c3c4c5;
	padding: 30px 7% 30px 3%;
}
#main-menu-sidebar {
	width: 100%;
}
#main-menu-sidebar > ul > li {
	list-style: none;
	display: block;
	padding: 0;
}
#main-menu-sidebar > ul > li > a {
	font-weight: 400;
	font-size: 1em;
	color: $font_color;
	text-decoration: none;
	transition-property: color;
	transition-duration: .5s;
	transition-timing-function: linear;
}
#main-menu-sidebar > ul > li > a:hover {
	color: #42b549;
}

#main-menu-sidebar li ul {
  list-style:none;
  text-align:center;
  font-size: 1em;
  margin:0;
  padding:0;
}
#main-menu-sidebar li ul li {
	background: #c3c4c5;
	transition-property: background-color;
	transition-duration: .5s;
	transition-timing-function: linear;
}
#main-menu-sidebar li ul li:hover {
	background-color: rgba(66,181,73,0.6);
}

#main-menu-sidebar li ul li:last-child {
	border-bottom: none;
}
#main-menu-sidebar li ul li a {
	padding: 5px 30px;
  display: block;
  color: $font_color;
	text-decoration: none;
}

div.burg-menu {
	display: none;
	float: left;
	margin: 17px 0 0 10%;
}
.toggle-mnu {
  display: block;
  width: 28px;
  height: 28px;
  margin-top: 14px;
}
.toggle-mnu span:after, .toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px;
}
.toggle-mnu span:after {
  top: 18px;
}
.toggle-mnu span {
  position: relative;
  display: block;
}
.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
  width: 100%;
  height: 2px;
  background-color: #1337a4;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}
.toggle-mnu.on span {
  background-color: transparent;
}
.toggle-mnu.on span:before {
  transform: rotate(45deg) translate(-1px, 0px);
}
.toggle-mnu.on span:after {
  transform: rotate(-45deg) translate(6px, -7px);
}





.error {
	border: 2px solid #f44242 !important;
}

.not_error {
	border: 1px solid #fff !important;
}
.slider-hide {
	display: none;
}

.clearfix::after {
	content: "";
	display: table;
	clear: both;
}